import Vue from 'vue'
import Router from 'vue-router'
import login from '@/pages/register&login/components/login'
import register from "../components/register";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },{
    path:'/register/:isregister',
      name: 'register',
      component:register
    }
  ]
})



